.banner{
    height: 490px;
    color: white;
    object-fit: contain;
}

.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: .3rem;
}
.banner_description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: .8rem;
    max-width: 360px;
    height: 80px;
}

.banner_button{
    cursor: pointer;
    outline: none;
    border: none;
    color: #fff;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-right: 2rem;
    padding-left: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: .5rem;
    background-color: rgba(51,51,51,.5);
}
.banner_button:hover{
 color: #000;
 background-color: #e6e6e6;
 transition: all .2s;
}

.banner--fadeBottom{
    height: 10.0rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37,.61),
        #111

    );

}